body,
html {
    height: 100%
}

body {
    background: #eff0f0;
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    color: #000
}

.wrapper {
    margin: 0 auto;
    padding: 1em
}

h1 {
    color: #063754;
    margin: 1em .5em;
    position: relative;
}


